<template>
    <div
        ref="wraper"
        class="script-version-basic-box"
        v-bkloading="{ isLoading }">
        <template v-if="data.id">
            <div class="script-type-flag">
                <img :src="`/static/images/script/${data.typeName}.svg`">
            </div>
            <div class="detail-column" style="width: 270px;">
                <div class="detail-col">
                    <div class="item-label">{{ '脚本名：' }}</div>
                    <div class="item-value">
                        <auth-component
                            auth="script/edit"
                            :resource-id="data.id">
                            <jb-edit-input
                                style="width: 100%;"
                                field="scriptName"
                                :value="data.name"
                                :remote-hander="val => handleUpdateScript('scriptName', val)" />
                            <div slot="forbid">{{ data.name }}</div>
                        </auth-component>
                    </div>
                </div>
                <div class="detail-col">
                    <div class="item-label">{{ '更新人：' }}</div>
                    <div class="item-value">
                        <div class="text-box" v-bk-overflow-tips>{{ data.lastModifyUser }}</div>
                    </div>
                </div>
                <div class="detail-col">
                    <div class="item-label">{{ '创建人：' }}</div>
                    <div class="item-value">
                        <div class="text-box" v-bk-overflow-tips>{{ data.creator }}</div>
                    </div>
                </div>
            </div>
            <div class="detail-column" style="width: 282px;">
                <div class="detail-col">
                    <div class="item-label">{{ '脚本语言：' }}</div>
                    <div class="item-value">
                        <div class="text-box">{{ data.typeName }}</div>
                    </div>
                </div>
                <div class="detail-col">
                    <div class="item-label">{{ '更新时间：' }}</div>
                    <div class="item-value">
                        <div class="text-box" v-bk-overflow-tips>{{ data.lastModifyTime }}</div>
                    </div>
                </div>
                <div class="detail-col">
                    <div class="item-label">{{ '创建时间：' }}</div>
                    <div class="item-value" v-bk-overflow-tips>
                        <div class="text-box">{{ data.createTime }}</div>
                    </div>
                </div>
            </div>
            <div class="detail-column last">
                <div class="detail-col">
                    <div class="item-label">{{ '场景标签：' }}</div>
                    <div class="item-value">
                        <auth-component
                            auth="script/edit"
                            :resource-id="data.id">
                            <jb-edit-tag
                                class="input"
                                field="scriptTags"
                                :rows="1"
                                :value="data.tags"
                                :remote-hander="val => handleUpdateScript('scriptTags', val)" />
                            <div slot="forbid">{{ data.tagText }}</div>
                        </auth-component>
                    </div>
                </div>
                <div class="detail-col">
                    <div class="item-label">{{ '脚本描述：' }}</div>
                    <div class="item-value">
                        <jb-edit-textarea
                            field="scriptDesc"
                            single-row-render
                            :placeholder="'在此处标注该脚本的备注和使用说明'"
                            :maxlength="500"
                            :rows="1"
                            :value="data.description"
                            :remote-hander="val => handleUpdateScript('scriptDesc', val)" />
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>
<script>
    import ScriptService from '@service/script-manage';
    import PublicScriptService from '@service/public-script-manage';
    import { checkPublicScript } from '@utils/assist';
    import JbEditInput from '@components/jb-edit/input';
    import JbEditTag from '@components/jb-edit/tag';
    import JbEditTextarea from '@components/jb-edit/textarea';

    export default {
        name: '',
        components: {
            JbEditInput,
            JbEditTag,
            JbEditTextarea,
        },
        data () {
            return {
                isLoading: true,
                data: {},
            };
        },
        created () {
            this.publicScript = checkPublicScript(this.$route);
            this.serviceHandler = this.publicScript ? PublicScriptService : ScriptService;
            this.scriptId = this.$route.params.id;
            this.fetchScriptBasic();
        },
        methods: {
            /**
             * @desc 获取脚本基本信息
             */
            fetchScriptBasic () {
                this.isLoading = true;
                this.serviceHandler.fetchBasicInfo({
                    id: this.scriptId,
                }).then((data) => {
                    this.data = Object.freeze(data);
                    this.calcLableWidth();
                })
                    .finally(() => {
                        this.isLoading = false;
                    });
            },
            /**
             * @desc 计算脚本lable的宽度
             */
            calcLableWidth () {
                this.$refs.wraper.querySelectorAll('.detail-column').forEach((columnEl) => {
                    const $lableEles = columnEl.querySelectorAll('.item-label');
                    let maxWidth = 0;
                    $lableEles.forEach((ele) => {
                        const { width } = ele.getBoundingClientRect();
                        maxWidth = Math.max(maxWidth, width);
                    });
                    $lableEles.forEach((ele) => {
                        ele.style.width = `${maxWidth + 2}px`;
                    });
                });
            },
            /**
             * @desc 更新脚本基本信息
             * @param {String} field 指定更新的字段名
             * @param {Object} payload 更新的字段key和value
             */
            handleUpdateScript (field, payload) {
                return this.serviceHandler.scriptUpdateMeta({
                    id: this.scriptId,
                    ...payload,
                    updateField: field,
                });
            },
        },
    };
</script>
